<template>
  <div class="about-page">
        <div @click="headAnimOut(true)" class="control" id="forward">F</div>
        <canvas id="threeScene" class="scene" ref="scene" />

        <div class="scroll-markers">
          <div class="scroll-marker-thick"></div>
          <div class="scroll-marker-thin"></div>
        </div>

        <!-- <transition>
          <div class="transition-section-wrapper">
            <div v-if="slideIndex == 0" class="transition-section first"></div>
            <div v-if="slideIndex == 1" class="transition-section second"></div>
            <div v-if="slideIndex == 2" class="transition-section third"></div>
          </div>
        </transition> -->


        <div class="transition-section-wrapper">
          <transition-group tag="div" class="transition-section" name="slide">
              <div 
                v-for="number in [slideIndex]" 
                v-bind:key="number"
                :class="slideClass(number)"
              ></div>
          </transition-group>        
        </div>

        <div class="anim-header-container">
          <h1 @click="headAnimOut" id="anim-header">{{textSlides[slideIndex]}}</h1>
        </div>

        <div @click="headAnimOut(false)" class="control" id="back">B</div>

  </div>
</template>

<script>
import anime from 'animejs'
import * as THREE from 'three';
//import { setInterval } from 'timers';

export default {
  name: 'About',
  data: function() {
    return {
      morphMesh: null,
      textSlides: [
        "alpha slide",
        "beta slide",
        "charlie slide",
        "delta slide"
      ],
      slideIndex: 0,
      isUnlocked: false,
    }
  },
  mounted: function(){
    this.initThree()

    var targetThick = document.querySelector('.scroll-marker-thick');
    var lockTweenThick = anime({
      targets: targetThick,
      autoplay: false,
      easing: 'linear',
      rotate: 263,
      duration: 7500,
      begin: (anim) => {},
      update: (prog) => {},
      complete: (anim) => {
        this.isUnlocked = true;
        this.headAnimIn();

       }
    });
    

    var targetThin = document.querySelector('.scroll-marker-thin');
    var lockTweenThin = anime({
      targets: targetThin,
      autoplay: false,
      easing: 'linear',
      rotate: 441,
      duration: 7500,
      begin: (anim) => {},
      update: (prog) => {},
      complete: (anim) => {
        this.isUnlocked = true;
       }
    });
    

    // document.body.onmousedown = () => { 
    //   if(!this.isUnlocked){
    //     lockTweenThick.play();
    //     lockTweenThin.play();
    //   }
    // };
    // document.body.touchstart = () => { 
    //   alert("hit")
    //   if(!this.isUnlocked){
    //     lockTweenThick.play();
    //     lockTweenThin.play();
    //   }
    // };


    // document.body.onmouseup = () => {
    //   if(!this.isUnlocked){
    //     lockTweenThick.pause()
    //     lockTweenThin.pause()
    //   }
    // };
    // document.body.touchend = () => {
    //   if(!this.isUnlocked){
    //     lockTweenThick.pause()
    //     lockTweenThin.pause()
    //   }
    // }  

  },
  methods: {

    initThree(){
        this.delta = 0;
        this.mouse = new THREE.Vector2();
        
        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera( 
            75, 
            window.innerWidth / window.innerHeight, 
            0.1, 
            1000 
        );

        this.renderer = new THREE.WebGLRenderer( { 
            canvas: document.getElementById( "threeScene" ), 
            alpha: true, 
            antialias: true, 
        } );
        this.renderer.setSize( window.innerWidth, window.innerHeight );

        var material = new THREE.MeshBasicMaterial({
          color: 0xffffff, 
          wireframe: true
        });

        var geometryBox = new THREE.SphereBufferGeometry( 1.5, 20, 15 );
        var cube = new THREE.Mesh( geometryBox, material );

        cube.position.z = -5;
        this.scene.add( cube );   

        this.animate()

    },

    animate: function(){
        this.renderer.render( this.scene, this.camera );
        requestAnimationFrame(this.animate);
    },

    slideClass: function(key){
      let className = 'slide-' + key;
      return className;
    },
    incrementSlides: function(){
      this.slideIndex++
    },
    
    decrementSlides: function(){
      this.slideIndex--
    },

    headAnimOut: function(isIncrement){
      let animeHead = document.getElementById('anim-header');

      anime({
        targets: animeHead,
        translateX: 25,
        opacity: 0,
        ease: 'linear',
        complete: (anim) => {
        },
        delay: 1500,
        complete: (anim) => {
          animeHead.style.transform = "translateX(-25px)";
          if(isIncrement){
            this.incrementSlides();
          } else {
            this.decrementSlides();
          }
          
          this.headAnimIn();
        }
      });

    },
    headAnimIn: function(){
      let animeHead = document.getElementById('anim-header');

      if(this.isUnlocked){
        anime({
          targets: animeHead,
          translateX: 0,
          opacity: 1,
          ease: 'linear',
          complete: (anim) => {
          },
          delay: 1500
        });
      }

    },

  }
  
}
</script>

