      } 

      &::after {
        position: absolute;
        right: -35px;
        bottom: 37.5%;
        content: "";
        height: 75px;
        width: 75px;
        border-radius: 100%;
        background: orange;
      }

    }    

  }

  .transition-section-wrapper{
    background: green;
    position: relative;
    height: 100%;
    width: 100%;

    .transition-section {
      position: relative;
      display: inline-block;
      height: 100%;
      width: 100%;

      div {
        @include abs-pos;
        z-index: 10;

        height: 100%;
        width: 100%;
        
      }

      & .slide-0 {
        background: blue;
      }

      & .slide-1 {
        background: yellow;
      }  
      
      & .slide-2 {
        background: purple;
      }      

    }

  }

  .anim-header-container {
    @include abs-pos;

